@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro:400,700&display=swap);
.gameboy {
    position: relative;
    display: inline-block;
    -webkit-animation: fadeUp 0.3s forwards ease-out;
            animation: fadeUp 0.3s forwards ease-out;
}

@-webkit-keyframes fadeUp {
    from {
        -webkit-transform: translateY(80%);
                transform: translateY(80%);
        opacity: 0.3;
    }
    to {
        -webkit-transform: translateY(10%);
                transform: translateY(10%);
        opacity: 1;
    }
}

@keyframes fadeUp {
    from {
        -webkit-transform: translateY(80%);
                transform: translateY(80%);
        opacity: 0.3;
    }
    to {
        -webkit-transform: translateY(10%);
                transform: translateY(10%);
        opacity: 1;
    }
}

.gameboy.disabled {
    opacity: 0.4;
    -webkit-filter: grayscale();
            filter: grayscale();
}

.gameboy.disabled canvas {
    opacity: 0.8;
}

.gameboy .background {
    box-shadow: 0 0 36px rgba(0, 0, 0, 0.3);
}

.gameboy canvas {
    position: absolute;
    left: calc(50% - 173px);
    top: 130px;
}
.actions {
    padding: 4px 0;
    padding-left: 8px;
    background-color: rgba(255, 255, 255, 0.1);
}

.actions button {
    background: none;
    border: none;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
    cursor: pointer;
}

.actions button svg {
    color: rgba(255, 255, 255, 0.8);
    width: 16px;
    height: 16px;
    -webkit-transition: color 0.2s;
    transition: color 0.2s;
}

.actions button:hover svg {
    color: white;
}

.debugger.disabled .actions button {
    opacity: 0.5;
}
.memory-map .line {
    display: grid;
    grid-template-columns: 64px 1fr;
    font-family: 'Source Code Pro', monospace;
    color: white;
}

.memory-map .current-line {
    background-color: rgba(255, 255, 255, 0.2)
}

.memory-map .breakpoint {
    background-color: #943d7a75;
    position: relative;
}

.marker {
    position: absolute;
    top: 0;
    left: 0;
    width: 48px;
    height: 20px;
}

.marker::after {
    content: ' ';
    position: absolute;
    top: 0;
    left: 48px;
    width: 0;
    height: 0;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
}

.line:hover .marker {
    background-color: rgba(255, 255, 255, 0.2);
}

.line:hover .marker::after {
    border-left: 10px solid rgba(255, 255, 255, 0.2);
}

.breakpoint .marker, .breakpoint:hover .marker {
    background-color: #b14891b6;
}

.breakpoint .marker::after, .breakpoint:hover .marker::after {
    border-left: 10px solid #b14891b6;
}

.memory-map .line.current-line.breakpoint {
    background-color: var(--primary)
}

.memory-map .line .line-number {
    padding-left: 8px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    opacity: 0.3;
}

.memory-map .line .line-mnemonic {
    color: rgb(116, 163, 250);
    margin-right: 4px;
}
.memory-map {
    width: 100%;
    height: 100%;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
}

.debugger.disabled .memory-map {
    opacity: 0.5;
}
.background-tile-map {
    border-bottom: 1px rgba(255, 255, 255, 0.2) solid;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
}

.debugger.disabled .background-tile-map {
    opacity: 0.5;
}
.debugger {
    background-color: rgb(41, 41, 41);
    display: grid;
    grid-template-rows: auto 1fr auto;
    box-shadow: 0 0 16px rgba(0, 0, 0, 0.2);
}

.debugger.disabled > * {
    pointer-events: none;
}
.emulator {
    width: 100%;
    height: 100%;
    display: grid;
    grid-auto-flow: column;
    grid-gap: 16px;
}

.gameboyContainer {
    display: flex;
    justify-content: center;
    overflow: hidden;
}
